@media (max-width: 767px) {
  .main-nav {
    position: relative;
  }
  .main-nav .main-nav__toggle {
    position: absolute;
    right: 0;
    top: -47px;
    padding: 0;
    display: block;
    width: 24px;
    height: 18px;
    background-color: transparent;
    border: none;
  }
  .main-nav .main-nav__toggle::after,
  .main-nav .main-nav__toggle::before {
    content: "";
    position: absolute;
    top: 8px;
    left: 0;
    width: 24px;
    height: 2px;
    background-color: #000000;
  }
  .main-nav .main-nav__toggle::after {
    box-shadow: 0 8px 0 0 #000000, 0 -8px 0 0 #000000;
  }
  .main-nav--open .main-nav__toggle {
    right: -1px;
    top: -49px;
  }
  .main-nav--open .main-nav__toggle::after {
    transform: rotate(45deg);
    box-shadow: none;
  }
  .main-nav--open .main-nav__toggle::before {
    transform: rotate(-45deg);
  }
  .main-nav--nojs .main-nav__toggle {
    display: none;
  }
  .main-nav--open .main-nav__list {
    display: block;
    margin: 0 -20px;
    text-align: center;
    border-bottom: 1px solid #e6e6e6;
  }
}
